@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
@import url("https://fonts.googleapis.com/css2?family=Gruppo&display=swap");
@import url("https://fonts.googleapis.com/css2?family=PT+Mono&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Space+Mono&display=swap");

.stop-watch-section {
  width: 650px;
  margin-top: 20px;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 15px;
  justify-content: center;
  align-items: center;
  font-family: "Gruppo", cursive;
  user-select: none;
  /* width: 0%; */
}

.timer {
  position: relative;
  width: 550px;
  height: 150px;
  padding-top: 20px;
  background: rgba(255, 255, 255, 0.19);
  z-index: 2;
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.19);
  backdrop-filter: blur(20px);
  transition: 2s -webkit-filter linear;
}
.timer .container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 75%;
}

.timer .backgroundContainer {
  position: absolute;
  top: 0;
  left: 0;
  padding-top: 20px;
  border-radius: 20px;
  width: 550px;
  height: 150px;
  background-color: rgba(0, 0, 0, 0.25);
  border: 1px solid rgba(255, 255, 255, 0.01);
}
.stop-watch-section h1 {
  display: block;
  max-width: 550px;
  box-sizing: border-box;
  padding: 5px;
  font-family: "Raleway", sans-serif;
  font-size: 2em;
  color: #333;
  text-align: center;
  padding: 1px 7px 5px 15px;
  border-radius: 5px;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0, 0.29);
  margin: 5px 80px;
  margin-bottom: 10px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(237, 241, 244, 0.5);
  /* border: 2px solid rgba(237, 241, 244, 1); */
}

.stop-watch-section h1 span {
  font-size: 1.4em;
  font-family: "Gruppo", cursive;
  margin-right: 0;
  padding-right: 0;
}
.timer .container h2 {
  font-size: 5em;
  font-weight: 400;
  margin: -10px 0px 0px 0px;
  color: white;
  color: red;
  font-family: "PT Mono", monospace;
  font-family: "Space Mono", monospace;
}
.timer .container .block {
  text-align: center;
  padding: 5px 15px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.19);
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0, 0.2);
  margin: 0 10px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  height: 100px;
}

.timer .container span {
  font-size: 1.2em;
  font-weight: 600;
  color: white;
  font-family: "Gruppo", cursive;
}

.set-timer {
  font-size: 1.25em;
  color: white;
}
.set-timer input {
  font-size: 1em;
  color: white;
  text-align: center;
  padding: 5px 15px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.05);
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0, 0.2);
  margin: 0 10px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}

.stop-watch-section .button_block {
  width: 450px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-evenly;
  border-radius: 10px;
  background: #edf1f4;
  display: flex;
  padding: 20px;
}

.stop-watch-section button {
  width: 130px;
  height: 50px;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
  user-select: none;
  font-size: 1.2em;
  color: #666;
  border: 2px solid #edf1f4;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1), -5px -5px 10px #fff;
  border-radius: 10px;
}

.stop-watch-section button:active {
  box-shadow: inset 5px 5px 10px rgba(0, 0, 0, 0.1), inset -5px -5px 10px #fff;
  color: #f44336;
}

.stop-watch-section button:disabled {
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1), -5px -5px 10px #fff;
  color: #666;
}
@media screen and (max-width: 1024px) {
  .stop-watch-section {
    width: 100%;
    position: absolute;
    left: 0;
  }
}
@media screen and (max-width: 767px) {
  .timer,
  .timer .backgroundContainer {
    width: 90%;
    padding-top: 15px;
    height: 125px;
  }
  .timer .container .block {
    height: 75px;
    padding: 10px;
    padding-top: 2px;
    padding-bottom: 2px;
  }
  .timer .container h2 {
    font-size: 4em;
    margin-bottom: 0px;
    /* height: 100px; */
  }
  .stop-watch-section h1 {
    width: 75%;
    padding-right: 5px;
  }
  .stop-watch-section .button_block {
    width: 90%;
  }
}
