@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);

.App {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-around;
  margin: 0;
  padding: 0;
  position: relative;
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
}

.screen_saver {
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  text-align: center;
  z-index: 3;
}

.textBlock {
  background: rgba(255, 255, 255, 0.9);
  padding: 50px;
  width: 80%;
  margin: 0 auto;
  margin-top: 200px;
  animation: 1s 1 opacity;
}
.screen_saver p {
  font-size: 1.4em;
  font-family: "Raleway", sans-serif;
  color: #444;
  margin: 5px auto;
}

.textBlock p:nth-of-type(1) {
  animation: 1s 1 opacity;
}
.textBlock p:nth-of-type(2) {
  animation: 2s 1 opacity;
}
.textBlock p:nth-of-type(3) {
  animation: 3s 1 opacity;
}
.textBlock p:nth-of-type(4) {
  animation: 4s 1 opacity;
}
.textBlock p:nth-of-type(5) {
  animation: 5s 1 opacity;
}

@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes transform {
  from {
    transform: translateY(-150vh);
  }
  to {
    transform: translateY(0);
  }
}

.changeBackground_btn {
  position: absolute;
  top: 5px;
  right: 5px;
  background: none;
  border: none;
  width: 50px;
  height: 50px;
}
.back_btn {
  position: absolute;
  top: 2px;
  left: 0px;
  background: none;
  border: none;
  width: 50px;
  height: 50px;
  /* transform: translateX(1250%); */
}
.back_btn span {
  margin-left: -10px;
}

.changeBackground_btn:active .material-symbols-outlined {
  transform: rotate(180deg);
  transition: all 0.5s;
}

.changeBackground_btn .material-symbols-outlined {
  font-variation-settings: "FILL" 1, "wght" 700, "GRAD" 0, "opsz" 48;
  background: -webkit-linear-gradient(
    45deg,
    rgba(242, 246, 248, 0.1) 0%,
    rgba(216, 225, 231, 1) 10%,
    rgb(108, 121, 129) 80%,
    rgba(224, 239, 249, 0.4) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 2.5em;
}

.back_btn .material-symbols-outlined {
  font-variation-settings: "FILL" 1, "wght" 700, "GRAD" 0, "opsz" 1;
  background: -webkit-linear-gradient(
    45deg,
    rgba(242, 246, 248, 0.1) 0%,
    rgba(216, 225, 231, 1) 10%,
    rgb(108, 121, 129) 80%,
    rgba(224, 239, 249, 0.4) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 4em;
}

.back_btn:active .material-symbols-outlined {
  transform: translate(-50%);
  opacity: 0;
  transition: all 0.4s;
  /* transition: transform 5s; */
}

@media screen and (max-width: 767px) {
  .App {
    justify-content: space-between;
  }
  .textBlock {
    background: rgba(255, 255, 255, 0.9);
    padding: 50px;
    width: 60%;
    margin: 0 auto;
    margin-top: 50px;
    animation: 1s 1 opacity;
  }
  .screen_saver p {
    font-size: 1.2em;
    font-family: "Raleway", sans-serif;
    color: #444;
    margin: 5px auto;
  }
}
